:root {
    --backdrop-visible-background: var(--color-light-bg-overlay);
    --backdrop-hidden-background: transparent;
}
:root {
    --drawer-width: 500px;
}
.drawer__component_c9bud {
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: var(--drawer-width);
    flex: 1;
    align-self: flex-end;
    overflow: auto;
    will-change: transform;
}
.drawer__content_c9bud {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;
}
/* enter */
.drawer__enter_c9bud {
    transform: translateX(100%);
}
.drawer__contentEnter_c9bud {
    opacity: 0;
}
.drawer__backdropEnter_c9bud {
    background-color: var(--backdrop-hidden-background);
}
.drawer__enterActive_c9bud {
    transition: transform 0.3s ease-in-out;
    transform: translateX(0);
}
.drawer__backdropEnterActive_c9bud,
.drawer__backdropEnterDone_c9bud {
    transition: background 0.3s ease-in-out;
    background-color: var(--backdrop-visible-background);
}
.drawer__contentEnterActive_c9bud {
    transition: opacity 0.2s ease-in-out 0.3s;
    opacity: 1;
}
/* exit */
.drawer__exit_c9bud {
    transform: translateX(0);
}
.drawer__backdropExit_c9bud {
    background-color: var(--backdrop-visible-background);
}
.drawer__contentExit_c9bud {
    opacity: 1;
}
.drawer__exitActive_c9bud {
    transition: transform 0.25s ease-in-out 0.1s;
    transform: translateX(100%);
}
.drawer__backdropExitActive_c9bud,
.drawer__backdropExitDone_c9bud {
    transition: background 0.25s ease-in-out 0.1s;
    background-color: var(--backdrop-hidden-background);
}
.drawer__contentExitActive_c9bud {
    opacity: 0;
    transition: opacity 0.12s ease-in-out;
}
